@import url('https://fonts.googleapis.com/css2?family=Archivo+Narrow:ital,wght@0,400;0,700;1,400;1,700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  /* background-color: blue; */
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

.main {
  position: relative;
  height: 100%;
  width: 100%;

}

.main-background {
  position: absolute;
  background-image: url('/public/images/samuel-cruz-TPD1WXQIfzc-unsplash.jpg');
  background-position: center;
  background-size: cover;
  filter: grayscale(.5) brightness(.8) contrast(2);
  width: 100%;
  height: 100%;
  z-index: -1;
}

.fade-background {
  filter: grayscale(.5) brightness(.6) contrast(2);
}

.full-container {
  position: fixed;
  height: 100%;
  width: 100%;
}

.navigation {
  position: relative;
  height: 100%;
  width: 100%;

}

.block-button {
  height: 50px;
  aspect-ratio: 1/1;
  position: absolute;
  right: 0;
  margin: 20px;
  margin-right: 2%;
  border: none;
  border-radius: 2px;
  background-color: #fff;
  font-size: 30px;
}

.block-button:hover {
  background-color: #000;
  border: 1px solid #fff;
  color: #fff;
}

.block-button:active {
  border: none;
  font-size: 25px;
}

.block {
  position: absolute;
  /* background-color: #000; */
}

.block1 {
  /* background-color: #f003; */
  background: 
  linear-gradient(60deg, red 400px, transparent 400px, transparent 410px, red 410px, red 440px, transparent 440px, transparent 450px, red 450px, red 470px, transparent 470px, transparent 480px, red 480px, red 490px, transparent 490px, transparent 500px, red 500px, red 505px, transparent 505px);
  width: 100%;
  height: 100%;
}

.nav-menu {
  margin-top: auto;
  margin-bottom: 0;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: flex-end;
}

.nav-menu ul {
  width: 100%;
  list-style-type: none;
  margin-bottom: 30px;
  /* background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
  background-color: white; */
}

.nav-menu ul li {
  width: 100%;
  
 
}

.nav-menu ul li a {
  text-decoration: none;
  /* color: #f00; */
  font-family: "Archivo Narrow";
  font-size: 60px;
  font-weight: 600;
  margin-left: 30px;
  /* background-clip: text;
  -webkit-background-clip: text;
  color: transparent; */


}

.nav-menu ul li:nth-of-type(1):hover{
  width: 100%;
  /* background: 
  linear-gradient(60deg, white 50%, transparent 50%, transparent 51%, white 51%, white 54%, transparent 54%, transparent 55%, white 55%, white 57%, transparent 57%, transparent 58%, white 58%, white 60%, transparent 60%, transparent 61%, white 61%, white 62%, transparent 62%);
  */
  /* background-color: red; */
  background: 
  linear-gradient(60deg, white 224px, transparent 224px, transparent 233px, white 233px, white 263px, transparent 263px, transparent 273px, white 273px, white 294px, transparent 294px, transparent 302px, white 302px, white 314px, transparent 314px, transparent 322px, white 322px, white 328px, transparent 328px);
} 

.nav-menu ul li:nth-of-type(2):hover{
  width: 100%;
  background: 
  linear-gradient(60deg, white 264px, transparent 264px, transparent 273px, white 273px, white 304px, transparent 304px, transparent 314px, white 314px, white 334px, transparent 334px, transparent 344px, white 344px, white 354px, transparent 354px, transparent 363px, white 363px, white 370px, transparent 370px);
 
  /* background-color: red; */
  
} 

.nav-menu ul li:nth-of-type(3):hover{
  width: 100%;
  background: 
  linear-gradient(60deg, white 304px, transparent 304px, transparent 313px, white 313px, white 344px, transparent 344px, transparent 353px, white 353px, white 374px, transparent 374px, transparent 383px, white 383px, white 394px, transparent 394px, transparent 403px, white 403px, white 409px, transparent 409px);
 
  /* background-color: red; */
  
} 

.nav-menu ul li:nth-of-type(4):hover{
  width: 100%;
  background: 
  linear-gradient(60deg, white 344px, transparent 344px, transparent 353px, white 353px, white 384px, transparent 384px, transparent 394px, white 394px, white 414px, transparent 414px, transparent 424px, white 424px, white 434px, transparent 434px, transparent 444px, white 444px, white 449px, transparent 449px);
 
  /* background-color: red; */
  
} 

.nav-menu ul li:nth-of-type(5){
  width: 100%;
  background: 
  linear-gradient(60deg, white 344px, transparent 344px, transparent 353px, white 353px, white 384px, transparent 384px, transparent 394px, white 394px, white 414px, transparent 414px, transparent 424px, white 424px, white 434px, transparent 434px, transparent 444px, white 444px, white 449px, transparent 449px);
 
  /* background-color: red; */
  
} 

.nav-menu ul li:hover a{
  color: #000;
  
}
