@media (orientation: landscape) {

  header h1 {
    font-size: 50px;
    margin-right: 140px;
  }

  .block1-down {
    transform: translate(-100%, 100%) rotate(-45deg);
    transition: 500ms ease;
  }
  
  .block2-down {
    transform: translate(-100%, 100%) rotate(-45deg);
    transition: 600ms ease;
  }
/*   
  .block3-down {
    transform: translate(-100%, 100%) rotate(-45deg);
    transition: 700ms ease;
  } */
  
  .block4-down {
    transform: translate(-100%, 100%) rotate(-45deg);
    transition: 700ms ease;
  }
 
  .block1-up {
    transform: translate(100px, -100px) rotate(-45deg);
    transition: 500ms ease;
  }
  
  .block2-up {
    transform: translate(850px, -250px) rotate(-45deg);
    transition: 600ms ease;
  }
  
  /* .block3-up {
    transform: translate(810px, -500px) rotate(-45deg);
    transition: 700ms ease;
  } */
  
  .block4-up {
    /* width: 500px; */
    transform: translate(250px, -450px) rotate(-45deg);
    transition: 700ms ease;
  }

}

header {
  height: 10vw;
  min-height: 75px;
  max-height: 120px;
  width: 100%;
  /* text-align: center; */
  background-color: #000;
  color: #fff;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.header-border {
  border-bottom: 1px solid white;
}

header h1 {
  font-size: 50px;
  font-family: "Archivo Narrow";
  font-weight: 700;
  /* margin-right: 100px; */
}

header h2 {
  font-size: 25px;
  font-family: "Archivo Narrow";
  font-weight: 500;
  width: 50px;
  height: 50px;
  color: #fff;
  margin-right: 80px;
  margin-left: 10px;
  position: relative;
  /* text-align: center; */
  /* background-color: #555; */
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #fff;
}

header h2 span {
  position: absolute;
  font-size: 16px;
}

.sp-1 {
  transform: translate(-13px, -10px);
  font-size: 30px;
}

.sp-2 {
  transform: translate(13px, 10px);
  font-size: 30px;

}

.home {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.navbar {
  z-index: 99;
  height: 100%;
  width: 100%;
  /* background-color: #f002; */

}
